.button {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 100%;
  height: 60px;
  background-color: $c-orange;
  border: none;
  transition: all 0.3s;

  font-weight: 600;
  line-height: 100%;
  color: $c-white;
  text-decoration: none;

  @include onTablet {
    margin: 0;
  }

  @include onDesktop {
    width: 270px;
  }

  &:hover {
    background-color: $c-btn-hover;
  }

  &:active {
    background-color: $c-btn-active;
  }

  &__alternate {
    display: none;
    background-color: transparent;
    border: 1px solid $c-orange;
    color: $c-orange;

    @include onTablet {
      display: flex;
    }

    &--hidden {
      display: flex;

      @include onTablet {
        display: none;
      }
    }

    &-arrow {
      stroke: $c-orange;
      margin-left: 8px;
    }

    &:hover {
      background-color: transparent;
      border: 1px solid $c-btn-hover;
      color: $c-btn-hover;
    }

    &:hover &-arrow {
      stroke: $c-btn-hover;
      animation: arrow-movement 0.3s ease-in;
    }

    &:active {
      background-color: transparent;
      border: 1px solid $c-btn-active;
      color: $c-btn-active;
    }

    &:active &-arrow {
      stroke: $c-btn-active;
    }
  }
}
